@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&family=Pacifico&display=swap);
body {
  margin: 0;
  font-family: 'Montserrat', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
  font-family: 'Pacifico', cursive;
  font-size: 5em;
  margin: 15px;
  color: rgb(77, 77, 77)
}

.App {
  text-align: center;
}

.card {
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  border-radius: 5px;
  width: 80%;
  margin: 0 auto;
}

.question-list {
    margin-top: 3em;
}

.question-list h2 {
    font-size: 2.5em;
    color: #223047;
    margin-bottom: 0
}

.list-options {
    border-bottom: 0.05em solid #223047;
    width: 80%;
    margin: 0 auto;
    margin-bottom: 0.75em;
    font-size: 1.2em;
    display: flex;
    justify-content: space-between;
}

.list-options button {
    background-color: transparent;
    border: none;
    color: rgb(77, 77, 77);
}

.list-options button:hover {
    background-color: #657288;
    color: #ffffff;
}

button.active-filter {
    background-color: #223047;
    color: #ffffff;
}

.filters {
    display: inline-block;
    text-align: left;
    margin: 0;
    padding: 0;
}

.filters p {
    display: inline-block;
    margin: 0;
    margin-right: 0.8em;
    font-size: 1em;
}

button.delete-all-btn:hover {
    background-color: rgb(110, 0, 0);
    color: #ffffff
}

.question {
    padding: 0.9em;
    margin: 0;
}

.hover {
    background-color: rgba(105, 105, 105, 0.445);
}

.answer-div {
    background-color: #408c99;
    margin: 0 auto;
    width: 80%;
    color: white;
}

.showing {
    opacity: 1;
    transform: scaleY(1);
    transform-origin: top;
    transition: transform 350ms ease 0ms, opacity 400ms ease;
}

.hiding {
    opacity: 0;
    transform: scaleY(0);
    transform-origin: top;
    transition: transform 350ms ease 0ms, opacity 400ms ease;
}

.answer {
    margin-top: 0;
    padding: 0.5em 0;
}

.item-options {
    padding: 0.6em 0;
}

button {
    display: inline-block;
    cursor: pointer;
    color: #ffffff;
    font-size: 0.8em;
    padding: 0.6em 1em;
    margin: 0 0.5em;
    text-decoration: none;
    outline: none;
}

.answer-btn {
    background-color: #599bb3;
    border: 0.06em solid #29668f;
    text-shadow: 0em 0.06em 0em #3d768a;
}

.answer-btn:hover {
    background-color: #408c99;
}

.detail-btn {
    background-color: #fc8d83;
    border: 0.06em solid #d83526;
    text-shadow: 0em 0.06em 0em #b23e35;
}

.detail-btn:hover {
    background-color: #e4685d;
}
form {
    width: 80%;
    margin: 0 auto;
    padding: 1em 0;
    text-align: left;
}

input[type=search], select {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
}

form label {
    font-size: 12px;
}

.form-btn {
    display: flex;
    justify-content: flex-end;
    margin-top: 0.75em;
}

.btn {
    display: inline-block;
    cursor: pointer;
    color: #ffffff;
    font-size: 0.65em;
    padding: 0.5em 0.9em;
    outline: none;
    margin: 0 0.5em;
}

#btn-save {
    background-color: #74ad5a;
    border: 0.06em solid #3b6e22;
    text-shadow: 0em 0.06em 0em #92b879;
}

#btn-save:hover {
    background-color: #68a54b;
}

#btn-clear {
    background-color: #bd5f5f;
    border: 0.06em solid #6e2222;
    text-shadow: 0em 0.06em 0em #b87979;
}

#btn-clear:hover {
    background-color: #a54b4b;
}

.form-row {
    display: flex;
    justify-content: space-between
}

.form-text {
    width: 75%
}

.form-category {
    width: 25%;
    padding: 0 8px;
}

input[type=radio] {
    display: none;
}

.star {
    cursor: pointer;
    transition: color 200ms;
}

.form-rating {
    width: 25%;
    padding: 0 8px;
}

.rating {
    margin: 8px;
    padding: 5px 0px;
}
.spinner:before {
  content: "";
  box-sizing: border-box;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 60px;
  height: 60px;
  margin-top: -30px;
  margin-left: -30px;
  border-radius: 50%;
  border-top: 3px solid #223047;
  border-right: 3px solid transparent;
  -webkit-animation: spinner 0.5s linear infinite;
          animation: spinner 0.5s linear infinite;
}

@-webkit-keyframes spinner {
  to {
    transform: rotate(360deg);
  }
}

@keyframes spinner {
  to {
    transform: rotate(360deg);
  }
}

.loading-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #F8F8F8AD
}
.details-modal {
    border: 1px solid black;
    z-index: 1;
    width: 50%;
    margin: 0 auto;
    padding: 8px;
    color: black;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    border-radius: 5px;
    background: white;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) !important;
}

.close-modal-btn {
    background: none;
    border: 0;
    color: rgb(151, 0, 0);
    font-size: 1.5em;
    width: 1.5em;
    height: 1.5em;
    padding: 0;
    float: right;
}

.close-modal-btn:hover, .delete-item-btn:hover {
    background-color: rgb(110, 0, 0);
    color: white;
}

.modal-header {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #223047;
    padding: 0.2em;
    align-items: center
}

.modal-header h3 {
    margin: 0;
    font-size: 1.5em;
    color: #223047
}

.modal {
    -webkit-backdrop-filter: grayscale(0.5) opacity(0.8) blur(0.1em);
            backdrop-filter: grayscale(0.5) opacity(0.8) blur(0.1em);
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.modal-content {
    width: 75%;
    margin: 0 auto;
    margin-top: 2em;
    margin-bottom: 2em;
}

.modal-content p {
    margin-bottom: 0.5em;
}

.modal-content-label {
    font-size: 0.7em;
    text-align: left;
    border-top: 1px dashed #223047;
    margin-top: 0;
}

.delete-item-btn {
    background-color: rgb(151, 0, 0);
    border: none;
    color: white;
    padding: 0.9em 1.2em;
    font-size: 0.6em;
    margin-bottom: 0.9em
}

.edit-item-btn {
    background-color: #223047;
    border: none;
    color: white;
    padding: 0.9em 1.2em;
    font-size: 0.6em;
    margin-bottom: 0.9em
}

.modal-rating-category {
    display: flex;
    justify-content: space-between
}

.modal-category {
    width: 50%;
    padding-right: 8px;
}

.modal-rating {
    width: 50%;
    padding-left: 8px;
}

.rating-view {
    margin-top: 0.5em;
    margin-bottom: 0.5em;
}

.btn-container {
    width: 75%;    
    margin: 0 auto;
    margin-top: 0.8em;
    display: flex;
    justify-content: space-between;
}
/* The snackbar - position it at the bottom and in the middle of the screen */
#snackbar {
  visibility: hidden; 
  min-width: 250px; 
  margin-left: -125px; 
  /* background-color: #4caf50;  */
  color: #fff; 
  text-align: center; 
  border-radius: 2px; 
  padding: 12px; 
  position: fixed;
  z-index: 1; 
  left: 50%; 
  bottom: 30px;
  display: "flex";
  justify-content: space-between;
}

#snackbar.show {
  visibility: visible;
  -webkit-animation: fadein 0.5s, fadeout 0.5s 2.5s;
  animation: fadein 0.5s, fadeout 0.5s 2.5s;
}

#snackbar.success {
  background-color: #4caf50;
}

#snackbar.error {
  background-color: #f44336;
}

#snackbar p {
  display: inline-block;
  padding: 0 12px
}

/* Animations to fade the snackbar in and out */
@-webkit-keyframes fadein {
  from {bottom: 0; opacity: 0;}
  to {bottom: 30px; opacity: 1;}
}

@keyframes fadein {
  from {bottom: 0; opacity: 0;}
  to {bottom: 30px; opacity: 1;}
}

@-webkit-keyframes fadeout {
  from {bottom: 30px; opacity: 1;}
  to {bottom: 0; opacity: 0;}
}

@keyframes fadeout {
  from {bottom: 30px; opacity: 1;}
  to {bottom: 0; opacity: 0;}
}

.content {
  display: "flex";
  justify-content: "space-between"
}
